<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>更换头像</span>
    </div>
    <div>
      <!-- 图片，用来展示用户选择的头像 -->
      <img class="preview" v-if="avatar" :src="avatar" alt="" />
      <img class="preview" v-else src="../../../assets/images/avatar.jpg" alt="" />

      <!-- 按钮区域 -->
      <div class="btn-box">
          <input @change="changeHandler" ref="inpimg" style="display:none" type="file" accept="image/*">
        <el-button @click="$refs.inpimg.click()" type="primary" icon="el-icon-plus">选择图片</el-button>
        <el-button @click="uploadHandler" type="success" icon="el-icon-upload" :disabled='!avatar'>上传头像</el-button>
      </div>
    </div>
  </el-card>
</template>

<script>
export default {
  name: 'UserAvatar',
  data () {
    return {
      avatar: ''
    }
  },
  methods: {
    changeHandler (e) {
    //   console.log(e.target.files)
      if (e.target.files.length === 0) return
      // 用fileReader()来读取文件信息
      //   创建 FileReader 对象
      const fr = new FileReader()
      //   调用readAsDataURL 将File 对象转换为 base64 字符串
      fr.readAsDataURL(e.target.files[0])
      //   读取文件时异步操作,所以要事件来获取结果
      fr.onload = e => {
        // console.log(e.target.result)
        this.avatar = e.target.result
      }
    },
    async uploadHandler () {
      const { data: res } = await this.$http.patch('/my/update/avatar', { avatar: this.avatar })
      console.log(res)
      //   if (res.code !== 0) return this.$message.error(res.message)
      //   this.$message.success(res.message)
      this.$store.dispatch('getUserInfo')
    }
  }
}
</script>

<style lang="less" scoped>
.btn-box {
  margin-top: 10px;
}
.preview {
  object-fit: cover;
  width: 40%;
  height: 40%;
}
</style>
